:root {
  --primary: rgb(72, 146, 202);
}
.button-container {
  display: flex;
  flex-wrap: wrap; /* Allows buttons to wrap to the next line on smaller screens */
  gap: 20px; /* This adds space between buttons */
}
.btns {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn--primary {
  /* background-color: var(--primary); */
  color: #ffffff;
  border: 1px solid var(--primary);
}

.btn--outline {
  background-color: transparent;
  color: #ffffff;
  padding: 8px 20px;
  border: 2px solid var(--primary);
  transition: all 0.3s ease-out;
}
.btn--small {
  background-color: #fff;
  color: rgb(72, 146, 202);
  padding: 8px 20px;
  font-size: 16px;
}

.btn--medium {
  background-color: #fff;
  color: rgb(72, 146, 202);
  padding: 8px 20px;
  font-size: 20px;
}

.btn--large {
  padding: 12px 26px;
  font-size: 22px;
}
.btns--active {
  background-color: rgb(72, 146, 202);
  color: white;
}
.btn--small:hover,
.btn--large:hover,
.btn--medium:hover {
  transition: all 0.3s ease-out;
  background: #89aadc;
  color: #242424;
  transition: 250ms;
}
@media (max-width: 800px) {
  .btn--large,
  .btn--medium {
    font-size: 16px; /* Smaller font size for small screens */
    padding: 8px 10px;
    min-width: 100px;
  }

  .btn--small {
    font-size: 14px; /* Smaller font size for small screens */
    padding: 8px 10px;
    min-width: 100px;
  }
}
